@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;600;700&display=swap);
@import url(https://cdn.syncfusion.com/ej2/material.css);
.proposal-table th {
    font-size: 20px;
    font-weight: bold;
}

.proposal-img {
    width: 100px;
    height: 100px;
    border-radius: 5px;
    cursor: pointer;
}

.proposal-item td {
    vertical-align: middle;
}

.proposal-item:hover {
    background-color: rgba(0,0,0,.08);
    transition: all 0.2s;
}

.proposal-item .proposal-status {
    padding: 5px 10px;
    border-radius: 5px;
}

.proposal-item .proposal-status.pending {
    background-color: #5927e3;
    color: white;
}

.proposal-item .proposal-status.winning {
    background-color: chartreuse;
}

.proposal-item .proposal-status.not-win {
    background-color: brown;
    color: white;
}

.proposal-item th, .proposal-item td, .proposal-item span {
    font-size: 18px;
    font-weight: 600;
}

.proposal-item td .countdown {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
}
body {
    margin: 0;
    padding:0;
    font-family: "Open Sans", sans-serif;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

.sidebar {
  box-shadow: rgb(113 122 131 / 11%) 0px 7px 30px 0px;
}
.nav-item,
.navbar {
  z-index: 10000;
}
@media  screen and (max-width:800px) {
  .sidebar{
    z-index: 10000000;
  }
}

.e-dlg-center-center, .e-quick-popup-wrapper.e-device{
  z-index: 1000000 !important;
}

::-webkit-scrollbar {
  width: 6px;
}
::-webkit-scrollbar-thumb {
  background-color: rgb(216, 216, 216);
  border-radius: 40px;
}
::-webkit-scrollbar-track {
  background-color: transparent;
}

/* color-picker style  */

 #preview {
  background: transparent
    url('https://ej2.syncfusion.com/react/demos/src/color-picker/images/pen.png')
    no-repeat;
  display: inline-block;
